import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { DataQrEmergencia } from 'src/app/interfaces/data-qr-emergencia';
import { QrEmergenciaService } from 'src/app/services/qr-emergencia.service';

@Component({
  selector: 'app-pendientes',
  templateUrl: './pendientes.page.html',
  styleUrls: ['./pendientes.page.scss'],
})
export class PendientesPage implements OnInit {

  opcion: string;
  sedesQr :{id: IDBValidKey, data: DataQrEmergencia}[] = [];
  porEnviar: DataQrEmergencia[] = [];

  constructor(private router: Router, private qrEmergenciaService: QrEmergenciaService) { }

  ionViewWillEnter(): void {
    this.opcion = 'emergencia';
  }

  ionViewDidLeave(): void {
    this.opcion = undefined;
  }

  ngOnInit() {
    this.dataQrEmergencia();
  }

  async dataQrEmergencia() {
    this.qrEmergenciaService.getDataQrAll().subscribe(sedesQr => {
      this.sedesQr = sedesQr;
      this.sedesQr.forEach(sede => {
        if (sede.data.terminado) {
          this.porEnviar.push(sede.data);
        }
      });
      this.sedesQr = this.sedesQr.reverse();
    });
  }

  back() {
    this.router.navigate(['/inicio-emergencia']);
  }

  async delete(){
    this.qrEmergenciaService.deleteAllDataQr();
  }

  public getFechaYHora(fecha): string {
    const fechaYHora = new Date(fecha);

    const dia = String(fechaYHora.getDate()).padStart(2, '0');
    const mes = String(fechaYHora.getMonth() + 1).padStart(2, '0'); // Los meses en JavaScript comienzan desde 0
    const ano = fechaYHora.getFullYear();

    const horas = fechaYHora.getHours() > 12 ? fechaYHora.getHours() - 12 : fechaYHora.getHours();
    const minutos = String(fechaYHora.getMinutes()).padStart(2, '0');
    const segundos = String(fechaYHora.getSeconds()).padStart(2, '0');
    const ampm = fechaYHora.getHours() >= 12 ? 'PM' : 'AM';

    const fechaFormateada = `${dia}/${mes}/${ano}\n${horas}:${minutos}:${segundos} ${ampm}`;

    return fechaFormateada;
  }

}
